@import 'ScssHelpers/helpers-all.scss';

.our_team {
	@include bp($point_2, min) {
		padding-top: 12rem;
		padding-bottom: 2rem;
	}
}

.our_team__head {
	margin: 0 auto;
	text-align: center;

	@include bp($point_6, min) {
		max-width: 61rem;
	}

	@include bp($point_6 - 1) {
		max-width: 29.8rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 7.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 5.2rem;
		}
	}
}

.our_team__subtitle {
	@include subtitle;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.our_team__title {
	@include h2;

	font-family: $font_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.7rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}
}

.our_team__text {
	@include text;
}

.our_team__wrap {
	position: relative;

	@include bp($point_3) {
		padding-bottom: 4.4rem;
	}
}

.our_team__list {
	@include bp($point_2, min) {
		display: flex;
		align-items: stretch;
		flex-wrap: wrap;

		margin: 0 -2rem -2rem 0;
	}
}

.our_team__item {
	&.swiper-slide {
		height: auto;
	}

	@include bp($point_1, min) {
		width: 25%;
	}

	@include bp($point_1 - 1, $point_2) {
		width: 33.333%;
	}

	@include bp($point_2, min) {
		flex-shrink: 0;
		padding: 0 2rem 2rem 0;
	}
}

.our_team__item__wrap {
	height: 100%;
	display: flex;
	flex-direction: column;
	border-radius: $gl_radius;
	overflow: hidden;

	transition: border-radius 0.3s;

	&:hover,
	&:focus {
		border-radius: 20rem 20rem $gl_radius $gl_radius;
	}
}

.our_team__pic {
	position: relative;
	display: block;
	width: 100%;

	&::after {
		content: '';
		display: block;

		@include bp($point_4, min) {
			padding-bottom: 100%;
		}

		@include bp($point_4 - 1) {
			padding-bottom: 90%;
		}
	}
}

.our_team__img {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

.our_team__item_content {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;

	transition: background 0.3s ease;

	.our_team__item__wrap:hover & {
		background-color: $coral;
	}

	@include bp($point_2, min) {
		padding: 3.2rem 2.8rem 4.2rem;
	}

	@include bp($point_3) {
		padding: 2rem 2rem 2.5rem;
		background-color: $green;
	}
}

.our_team__item_name {
	font-weight: 700;
	font-family: $font_2;
	line-height: 120%;

	@include bp($point_2, min) {
		font-size: 2.8rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_3) {
			margin-bottom: 4px;
		}
	}
}

.our_team__item_position {
	@include text;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4px;
		}
	}
}

.our_team__item_link {
	@include text;
	color: $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4px;
		}
	}
}

.swiper-wrapper {
	.our_team & {
		width: auto;
	}
}
